<template><section><h1>克里金插值色斑图</h1>
<p><code v-pre>vc-kriging-map</code> 组件用于加载克里金插值色斑图，基于 <code v-pre>kriging.js</code> 和 <code v-pre>turf.js</code> 实现。</p>
<h2>示例</h2>
<h3>添加克里金插值色斑图到场景</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-kriging-map ref="krigingmap" v-if="values.length !== 0" :breaks="breaks" :values="values" :lngs="lngs" :lats="lats" :colors="colors" @ready="subReady" clipCoords="./statics/SampleData/shp/china/guilin.json">
        </vc-kriging-map>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu mapStyle="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
