      <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-kriging-map</span>
          <span class="hljs-attr">v-if</span>=<span class="hljs-string">"values.length !== 0"</span>
          <span class="hljs-attr">:breaks</span>=<span class="hljs-string">"breaks"</span>
          <span class="hljs-attr">:values</span>=<span class="hljs-string">"values"</span>
          <span class="hljs-attr">:lngs</span>=<span class="hljs-string">"lngs"</span>
          <span class="hljs-attr">:lats</span>=<span class="hljs-string">"lats"</span>
          <span class="hljs-attr">:colors</span>=<span class="hljs-string">"colors"</span>
          <span class="hljs-attr">@ready</span>=<span class="hljs-string">"subReady"</span>
          <span class="hljs-attr">clipCoords</span>=<span class="hljs-string">"./statics/SampleData/shp/guilin.json"</span>
        &gt;</span>
          <span class="hljs-comment">&lt;!-- &lt;vc-kriging-map ref="krigingmap" v-if="values.length !== 0" :breaks="breaks" :values="values" :lngs="lngs" :lats="lats" :colors="colors"      @ready="subReady" :clipCoords="[109.61826, 24.25883, 111.49207, 26.38528]"&gt; --&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-kriging-map</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-terrain-cesium</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-terrain-cesium</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      data() {
        <span class="hljs-keyword">return</span> {
          <span class="hljs-attr">values</span>: [],
          <span class="hljs-attr">lngs</span>: [],
          <span class="hljs-attr">lats</span>: [],
          <span class="hljs-attr">breaks</span>: [<span class="hljs-number">0.1</span>, <span class="hljs-number">10</span>, <span class="hljs-number">25</span>, <span class="hljs-number">50</span>, <span class="hljs-number">100</span>, <span class="hljs-number">250</span>, <span class="hljs-number">500</span>],
          <span class="hljs-attr">colors</span>: [<span class="hljs-string">'#A5F38D'</span>, <span class="hljs-string">'#3DB93F'</span>, <span class="hljs-string">'#63B8F9'</span>, <span class="hljs-string">'#0002E2'</span>, <span class="hljs-string">'#FA00FA'</span>, <span class="hljs-string">'#7F0140'</span>]
        }
