</code></pre>
<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>krigingModel</td>
<td>string</td>
<td><code v-pre>'exponential'</code></td>
<td><code v-pre>optional</code> 指定克里金模型名字，取 'gaussian', 'spherical', 'exponential'。</td>
</tr>
<tr>
<td>krigingSigma2</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定克里金插值 sigma 参数。</td>
</tr>
<tr>
<td>krigingAlpha</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定克里金插值 alpha 参数。</td>
</tr>
<tr>
<td>canvasAlpha</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> 指定渲染时 alpha 参数。</td>
</tr>
<tr>
<td>colors</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定色斑图分段颜色数组。</td>
</tr>
<tr>
<td>breaks</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定色斑图分段数组。</td>
</tr>
<tr>
<td>clipCoords</td>
<td>Array|String</td>
<td><code v-pre>[]</code></td>
<td><code v-pre>required</code> 指定色斑图裁剪的坐标数组或 json 文件地址。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定色斑图是否显示。</td>
</tr>
<tr>
<td>cell</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定插值时分辨率。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject }</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      data () {
        return {
          values: [],
          lngs: [],
          lats: [],
          breaks: [0.1, 10, 25, 50, 100, 250, 500],
          colors: ["#A5F38D", "#3DB93F", '#63B8F9', "#0002E2", "#FA00FA", "#7F0140"]
        }
      },
      methods: {
        async ready (cesiumInstance) {
          window.vm = this
          this.cesiumInstance = cesiumInstance
          const {Cesium, viewer} = this.cesiumInstance
          let data = await Cesium.Resource.fetchJson({url: './statics/SampleData/weather/precipitation/guilin.json'})
          let lngs = []
          let lats = []
          let values = []
          data.reduce((pre, cur) => {
            lngs.push(parseFloat(cur.lon))
            lats.push(parseFloat(cur.lat))
            values.push(parseFloat(cur.value))
          }, [])
          this.lngs = lngs
          this.lats = lats
          this.values = values
        },
        subReady ({ Cesium, viewer, cesiumObject }) {
          viewer.zoomTo(viewer.dataSources.get(0))
        }
      }
    }
  </script>